import React, {useState} from 'react';
import Truncate from "react-truncate";
import PhoneInput from 'react-phone-input-2';
import { useTranslation } from 'react-i18next';
import {object} from "prop-types";
import { backendUrl } from '../../../config/config';

import './index.scss';
import min from "../../../assets/page/Rolls/min.svg";
import max from "../../../assets/page/Rolls/max.svg";
import {ACTIONS} from "../../../context/providerContext";
import {useCustomStore} from "../../../context/useStore";
import Basket from "../../../components/Basket";

const HeaderBlock = ({food}) => {
    const {state, dispatch} = useCustomStore();
    const {t} = useTranslation();
    const [phone, setPhone] = useState('')
    const handleOnChange = (e) => {
        setPhone(e)
    }
    return (
        <div
            className={'header-slider'}
            key={food._id}
        >
            <div className={'header-slider_list-block'}>
                <div className={'list-content'}>
                    <div className={'list-content_header'}>
                        <h2>{food.name}</h2>
                        <p>({food.pieces} шт.) Вес {food.weight} г. </p>
                        <Truncate
                            lines={4}
                            ellipsis={<span>...</span>}
                        >
                            <span> {food.description}</span>
                        </Truncate>
                    </div>
                    <div className={'list-content_center'}>
                        <amp-img
                            src={min}
                            alt={'-'}
                            onClick={() => dispatch({type: ACTIONS.REMOVE_TO_BASKET, payload: food._id})}
                            width={'12'}
                            height={'62'}
                        >
                        </amp-img>
                        <span>
                                <p>
                                    {
                                        state.cart.basket!== undefined &&
                                        state.cart.basket.find(el => el.id === food._id) ?
                                            state.cart.basket.map(el => {
                                                if(el.id === food._id){
                                                    return el.countBasket
                                                }
                                            }) : 1
                                    }
                                </p>
                            </span>
                        <amp-img
                            src={max}
                            alt={'+'}
                            onClick={() =>  dispatch({type: ACTIONS.ADD_TO_BASKET, payload: food})}
                            width={'12'}
                            height={'62'}
                        >
                        </amp-img>
                    </div>
                    <div className={'list-content_footer'}>
                        <div className={'price'}>
                            <h2>{food.price} грн.</h2>
                            {
                                food.defaultPrice && <p>{food.defaultPrice} грн.</p>
                            }
                        </div>
                        <Basket title={'button.toOrder'} style={'open-modalTwo'} />
                        <div className={'phone-input'}>
                            <PhoneInput
                                country={'ua'}
                                inputExtraProps={{
                                    name: "phone",
                                    required: true,
                                    autoFocus: true
                                }}
                                value={phone}
                                onChange={e => handleOnChange(e)}
                            />
                            <button className={'price-button'}>
                                {t('button.orderInClick')}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
            <div className={'header-slider_image-block'}>
                <img
                    src={backendUrl + `/${food.image}`}
                    alt={food.name}
                />
            </div>
        </div>
    )
}

HeaderBlock.propTypes = {
    food: object.isRequired
}

export default HeaderBlock;